const maptostate = (state) => {
  return {
    DataUsers: state.manageusers.getDataUser,
  };
};
class HeadFormHakAkses extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
    };
  }

  componentDidMount() {
    this.props.dispatch(getDataUser());
  }

  render() {
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off">
        <div className="row">
          <div className="col-6">
            <Field
              id="user_id"
              name="user_id"
              onChange={this.props.getDataUsers}
              label="Pilih User Id"
              placeholder="Pilih User Id"
              options={this.props.DataUsers.map((list) => {
                let data = {
                  value: list.user_id,
                  name: list.usere_name,
                };
                return data;
              })}
              component={ReanderSelect}
            />
          </div>
          <div className="col-6">
            <Field
              id="nama_user"
              name="nama_user"
              component={ReanderField}
              type="text"
              label="Nama User"
              defaultValue={this.state.nama}
              placeholder="Masukan Nama User"
              readOnly={true}
            />
          </div>
          <div className="col-12" style={{ height: 220 }}>
            <DualListBox
              options={options}
              selected={this.props.selected}
              filterPlaceholder="Filter..."
              onChange={this.props.selectedAkses}
            />
          </div>

          <div className="col-12">
            <button
              className="btn btn-primary btn-block"
              disabled={this.props.isLoading}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
          </div>
        </div>
        {this.props.isLoading ? (
          <Loading
            loading
            background="rgba(0, 0, 0, 0.35)"
            loaderColor="rgba(94, 147, 117, 1)"
          />
        ) : null}
      </form>
    );
  }
}

HeadFormHakAkses = reduxForm({
  form: "HeadFormHakAkses",
  enableReinitialize: true,
  validate: ValidaasiHakAkses,
})(HeadFormHakAkses);
export default connect(maptostate, null)(HeadFormHakAkses);
